import { createStore as reduxCreateStore } from "redux"

const reducer = (state, action) => {
  if (action.type === `TOGGLESWITCHPAGE`) {
    return Object.assign({}, state, {
        isSwitching: !state.isSwitching
    })
  }
  if (action.type === `SETMENUPAGE`) {
    return Object.assign({}, state, {
        menuPage: action.page
    })
  }
  if (action.type === `SETPAGE`) {
    return Object.assign({}, state, {
        page: action.page
    })
  }
  if (action.type === `SETTOTALPAGES`) {
    return Object.assign({}, state, {
        totalPages: action.totalPages
    })
  }
  if (action.type === `RESETPROGRESSPAGE`) {
    return Object.assign({}, state, {
        progressPage: 1, 
        progressPageDisplay: 1
    })
  }
  if (action.type === `EDITPROGRESSPAGE`) {
    return Object.assign({}, state, {
        progressPage: state.progressPage + action.number
    })
  }
  if (action.type === `EDITPROGRESSPAGEDISPLAY`) {
    return Object.assign({}, state, {
        progressPageDisplay: state.progressPageDisplay + action.number
    })
  }
  if (action.type === `SETSWIPE`) {
    return Object.assign({}, state, {
        x: action.x,
        y: action.y
    })
  }
  return state
}

const initialState = { page: 'About', isSwitching: false, menuPage: 'About', progressPage: 1, progressPageDisplay: 1, totalPages: 2, x: 0, y:0 }

const createStore = () => reduxCreateStore(reducer, initialState)
export default createStore